import React, { useEffect, useState } from 'react';
import { useAppSelector } from '../redux';
import { setSelectedStation } from '../redux/app/action';
import { useDispatch } from 'react-redux';
import { Alert, AlertIcon, Box, CloseButton } from '@chakra-ui/react';

export default function SidePanel() {
    const dispatch = useDispatch();

    const [isOpen, setIsOpen] = useState(true);

    const selectedStation = useAppSelector(state => state.app.selectedStation);
    const { name } = useAppSelector(state => state.param.stn_list[selectedStation]);

    useEffect(() => {
        if (!['linestart', 'lineend'].includes(selectedStation)) {
            setIsOpen(true);
        }
    }, [selectedStation]);

    const handleClose = () => {
        setIsOpen(false);
        setTimeout(() => {
            dispatch(setSelectedStation('linestart'));
        }, 200);
    };

    return (
        <Box height="100%" width={isOpen ? 320 : 0} flexShrink={0} bg="gray.700" transition="0.3s">
            <Box width={320}>
                <Alert status="info" variant="solid" size="xs" pl={3} pr={1} pb={1} pt={1}>
                    <AlertIcon />
                    {name[0] + ' - ' + name[1]}
                    <CloseButton ml="auto" />
                </Alert>
            </Box>
        </Box>
    );
}
