import React from 'react';
import { setCurrentStation } from '../../redux/param/action';
import { setSelectedStation } from '../../redux/app/action';
import { useDispatch } from 'react-redux';
import { HStack, IconButton } from '@chakra-ui/react';
import { DeleteIcon, EditIcon, StarIcon } from '@chakra-ui/icons';

interface TableRowActionsProps {
    stationId: string;
}

export default function TableRowActions(props: TableRowActionsProps) {
    const { stationId } = props;

    const dispatch = useDispatch();

    const handleDelete = () => {};

    return (
        <HStack>
            <IconButton
                size="xs"
                aria-label="Set as current station"
                onClick={() => dispatch(setCurrentStation(stationId))}
                icon={<StarIcon />}
            />
            <IconButton
                size="xs"
                aria-label="Edit this station"
                onClick={() => dispatch(setSelectedStation(stationId))}
                icon={<EditIcon />}
            />
            <IconButton size="xs" aria-label="Delete this station" onClick={handleDelete} icon={<DeleteIcon />} />
        </HStack>
    );
}
